<template>
  <m-form
    title
    :showList="false"
    :isEditing.sync="isEditing"
    :listRoute="{name:'LadingsReport'}"
    :canDelete="canDelete"
    :canSubmit="canSubmit"
    :confirmBtnText="confirmBtnText"
    :cancelConfirmBtnText="cancelConfirmBtnText"
    :canConfirm="canConfirm"
    :canCancelConfirm="canCancelConfirm"
    @cancelConfirm="cancelConfirm"
    @confirm="confirm"
    @goToForm="getItemByPosition"
    @submit="submit"
    @delete="deleteItem"
    @clearForm="clearForm(true)"
  >
    <template #header-btns>
      <open-sanad-btn v-if="item.sanad" :sanad="item.sanad" />
    </template>
    <template>
      <v-row>
        <v-col cols="12" md="2">
          <v-text-field label="" v-model="item.local_id" :disabled="true" />
        </v-col>
        <v-col cols="12" md="4">
          <v-autocomplete
            :return-object="true"
            label=" "
            v-model="item.driving"
            :items="$store.state.drivings"
            item-text="title"
            item-value="id"
            :disabled="!isEditing"
          />
        </v-col>

        <v-col cols="12" md="2">
          <v-autocomplete
            :return-object="false"
            label=" "
            v-model="item.type"
            :disabled="!isEditing"
            :items="types"
            item-value="id"
          />
        </v-col>
      </v-row>
      <v-row v-if="item.type.includes('l')">
        <v-col cols="12">
          <v-card-title class="py-0"></v-card-title>
        </v-col>
        <template>
          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="true"
              label=" "
              v-model="remittance"
              :disabled="!isEditing"
              :items="remittances"
              :search-input.sync="remittanceSearch"
              item-text="title"
              item-value="id"
              clearable
            />

            <!-- :item-text="(item) => item.code + ' - ' + item.contractor.name" -->
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field
              label=" "
              v-model="item.lading_number"
              :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="2">
            <date
              label=" "
              v-model="item.sanad_date"
              :default="true"
              :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="2">
            <date
              label=" "
              v-model="item.lading_date"
              :default="true"
              :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <ware-select label="" v-model="item.ware" :disabled="!isEditing" />
          </v-col>

          <v-col cols="12" md="3">
            <account-select
              label=""
              v-model="item.contractor"
              :disabled="!isEditing"
              items-type="level3"
            />
          </v-col>
          <v-col cols="12" md="2">
            <money
              label="  "
              v-model="item.contractor_price"
              :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="2">
            <money label=" " v-model="item.fare_price" :disabled="!isEditing" />
          </v-col>
          <v-col cols="12" md="2">
            <money
              label=" "
              v-model="+item.contractor_price - +item.fare_price"
              :disabled="true"
            />
          </v-col>
          <v-col cols="12" md="3">
            <money label=" " v-model="item.driver_tip_price" :disabled="!isEditing" />
          </v-col>

          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="true"
              label="  "
              v-model="item.driver_tip_payer"
              :items="tipPayers"
              :disabled="!isEditing"
              item-text="title"
              item-value="id"
            />
          </v-col>

          <v-col cols="12" md="2">
            <money
              label="  "
              v-model="item.lading_bill_difference"
              :disabled="!isEditing"
            />
          </v-col>

          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="true"
              label="   "
              v-model="item.lading_bill_difference_receiver"
              :items="ladingBillDifferenceReceivers"
              :disabled="!isEditing"
              item-text="title"
              item-value="id"
            />
          </v-col>

          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="false"
              label="   "
              v-model="item.remittance_payment_method"
              :items="remittancePaymentMethods"
              :disabled="!isEditing"
            />
          </v-col>

          <v-col cols="12" md="2">
            <money
              label="  "
              v-model="item.origin_amount"
              :disabled="!isEditing"
              @input="!is_destination_amount_dirty?item.destination_amount = item.origin_amount:''"
            />
          </v-col>
          <v-col cols="12" md="2">
            <money
              label="  "
              v-model="item.destination_amount"
              :disabled="!isEditing"
              @input="is_destination_amount_dirty = true"
            />
          </v-col>
          <v-col cols="12" md="2">
            <city-select label="" v-model="item.origin" :disabled="!isEditing" />
          </v-col>
          <v-col cols="12" md="2">
            <city-select label="" v-model="item.destination" :disabled="!isEditing" />
          </v-col>
          <v-col cols="12" md="2">
            <m-file-input
              label=""
              :disabled="!isEditing"
              v-model="item.lading_attachment"
              :delete-attachment.sync="item.delete_lading_attachment"
            />
          </v-col>
          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="false"
              label=" "
              v-model="item.contractor_type"
              :items="contractorTypes"
              :disabled="!isEditing"
              item-text="title"
              item-value="id"
            />
          </v-col>

          <v-col cols="12" md="2">
            <v-autocomplete
              :return-object="true"
              v-if="item.contractor_type == 'cmp'"
              label="   "
              v-model="item.ware_type"
              :items="wareTypes"
              :disabled="!isEditing"
              item-text="title"
              item-value="id"
            />
          </v-col>

          <v-col cols="12" md="2">
            <v-text-field
              v-if="item.created_by"
              label=""
              disabled
              v-model="item.created_by.name"
            />
          </v-col>

          <v-col cols="12" md="6">
            <v-textarea
              label=" "
              v-model="item.lading_explanation"
              :disabled="!isEditing"
            />
          </v-col>
          <template v-if="item.driving">
            <v-col cols="12" md="2">
              <money label=" " :value="ladingTotalValue" disabled />
            </v-col>
            <v-col cols="12" md="2">
              <money
                :label="'  ' + (item.driving.car.owner == 'o'?'': '')"
                :value="companyCommissionIncome"
                disabled
              />
            </v-col>
            <v-col cols="12" md="2">
              <money
                :label="item.driving.car.owner == 'o'?'   ':' '"
                :value="carIncome"
                disabled
              />
            </v-col>
          </template>

          <v-col cols="12">
            <v-divider color="indigo" style="border-width: 2px;"></v-divider>
          </v-col>
        </template>
      </v-row>
      <v-row v-if="item.type.includes('b')">
        <v-col cols="12">
          <v-card-title class="py-0"> </v-card-title>
        </v-col>
        <v-col cols="12" md="2">
          <v-autocomplete
            :return-object="true"
            label=" "
            :items="ladingBillSeriesItems"
            v-model="ladingBillSeries"
            item-text="serial"
            item-value="id"
            no-filter
            :search-input.sync="ladingBillSearchInput"
            :disabled="!isEditing"
            @change="ladingBillSeries && (item.billNumber = ladingBillSeries.numbers.filter(o=> !o.is_revoked)[0])"
            clearable
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-autocomplete
            :return-object="true"
            label=" "
            :items="ladingBillSeries?ladingBillSeries.numbers.filter(o => !o.is_revoked):[]"
            v-model="item.billNumber"
            item-text="number"
            item-value="id"
            :disabled="!isEditing"
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <date
            label=" "
            v-model="item.bill_date"
            :default="true"
            :disabled="!isEditing"
          />
        </v-col>
        <v-col cols="12" md="2">
          <money label=" " v-model="item.bill_price" :disabled="!isEditing" />
        </v-col>

        <v-col cols="12" md="2">
          <money label=" " v-model="item.cargo_tip_price" :disabled="!isEditing" />
        </v-col>

        <v-col cols="12" md="2">
          <v-autocomplete
            :return-object="true"
            label=""
            v-model="item.association"
            :items="$store.state.associations"
            item-text="name"
            item-value="id"
            :disabled="!isEditing"
            @change="item.association_price = item.association.price"
          />
        </v-col>
        <v-col cols="12" md="2">
          <money label=" " v-model="item.association_price" :disabled="!isEditing" />
        </v-col>

        <v-col cols="12" md="2">
          <v-autocomplete
            :return-object="false"
            label=" "
            v-model="item.receive_type"
            :items="receiveTypes"
            :disabled="!isEditing"
          />
        </v-col>

        <v-col cols="12" md="4">
          <money label="   " :disabled="!isEditing" />
        </v-col>
        <v-col cols="12" md="2">
          <m-file-input
            label=""
            :disabled="!isEditing"
            v-model="item.bill_attachment"
            :delete-attachment.sync="item.delete_bill_attachment"
          />
        </v-col>

        <v-col cols="12" md="4">
          <v-textarea
            label=" "
            v-model="item.bill_explanation"
            :disabled="!isEditing"
          />
        </v-col>

        <v-col cols="6">
          <money label="  " :value="billTotalPrice" disabled />
        </v-col>
      </v-row>
    </template>
  </m-form>
</template>

<script>
import formsMixin from "@/mixin/forms";
import { MFormMixin } from "@bit/mmd-mostafaee.vue.m-form";
import GetApi from "./GetApi";
export default {
  props: {
    id: {
      default: null,
    },
  },
  mixins: [formsMixin, MFormMixin, GetApi],
  data() {
    return {
      baseUrl: "dashtbashi/ladings",
      appendSlash: true,
      permissionBasename: "lading",
      formData: true,
      hasList: false,
      hasIdProp: true,
      ladingBillSeries: null,
      ladingBillSeriesItems: [],
      ladingBillSearchInput: "",
      is_destination_amount_dirty: false,
      remittanceSearch: "",
      remittances: [],
      remittance: null,
      contractorTypes: [
        { id: "o", title: "" },
        { id: "cmp", title: "" },
      ],
      wareTypes: [
        { id: "b", title: " " },
        { id: "s", title: " " },
      ],
      types: [
        {
          text: "",
          id: "l",
        },
        {
          text: "   ",
          id: "lb",
        },
        {
          text: " ",
          id: "b",
        },
      ],
    };
  },
  computed: {
    billTotalPrice() {
      let value =
        +this.item.association_price +
        +this.item.bill_price +
        +this.item.cargo_tip_price;
      this.item.lading_bill_total_value = Math.round(value * 100 || 0) / 100;
      return value;
    },
    ladingTotalValue() {
      let value = this.item.destination_amount * this.item.contractor_price;
      this.item.lading_total_value = Math.round(value * 100 || 0) / 100;
      return value;
    },
    companyCommissionIncome() {
      let value =
        this.item.destination_amount *
        (this.item.contractor_price - this.item.fare_price);
      this.item.company_commission_income = Math.round(value * 100 || 0) / 100;
      return value;
    },
    carIncome() {
      let value = this.item.destination_amount * this.item.fare_price;
      this.item.car_income = Math.round(value * 100 || 0) / 100;
      return value;
    },
  },
  watch: {
    ladingBillSearchInput() {
      this.getLadingBillSeries(this.ladingBillSearchInput);
    },
    remittanceSearch() {
      if (this.remittanceSearch != null) {
        this.d.getRemittances(this.remittanceSearch);
      }
    },
    remittance() {
      if (this.remittance) {
        if (
          this.item.remittance &&
          this.item.remittance.id == this.remittance.id
        ) {
          return;
        }
        Object.keys(this.remittance).forEach((key) => {
          if (key == "id") {
            this.item.remittance = this.remittance.id;
          } else if (!["local_id"].includes(key)) {
            this.item[key] = null;
            this.$set(this.item, key, this.remittance[key]);
          }
        });
      }
    },
  },
  mounted() {
    this.getRemittances(this.remittanceSearch);
  },
  methods: {
    clearForm() {
      this.isEditing = true;
      this.item = this.getItemTemplate();
      this.changeRouteTo(null);
      this.remittance = null;
    },
    getItemTemplate() {
      return {
        type: "",
        remittance: {},
        association: {
          id: null,
          price: null,
        },
        association_price: 0,
        cargo_tip_price: 0,
        contractor_type: this.contractorTypes[0],
        lading_ware: null,
        lading_contractor_price: null,
        lading_contractor: null,
        lading_bill_total_value: 0,
        destination_amount: null,
      };
    },
    getData() {
      this.getDrivings();
      this.getAssociations();
      if (this.id) {
        this.getItem(this.id);
      }
    },
    getLadingBillSeries(search) {
      this.request({
        loading: false,
        url: this.endpoint("dashtbashi/ladingBillSeries/?limit=10&offset=0"),
        method: "get",
        params: {
          serial__icontains: search,
        },
        success: (data) => {
          this.ladingBillSeriesItems = data["results"];
        },
      });
    },
    setItem(item) {
      this.item = item;
      this.remittance = item.remittance;
      this.ladingBillSeries = item.billNumber ? item.billNumber.series : null;
      this.changeRouteTo(item.id);
    },
    getItemByPosition(pos) {
      this.request({
        url: this.endpoint("dashtbashi/ladings/byPosition"),
        method: "get",
        params: {
          id: this.id,
          position: pos,
        },
        success: (data) => {
          this.setItem(data);
        },
        error: (error) => {
          if (error.response.status == 404) {
            this.notify("  ", "warning");
          }
        },
      });
    },
  },
};
</script>

<style scoped lang="scss">
</style>

