<template>
  <v-card>
    <v-card-title>     </v-card-title>
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <v-autocomplete
            :return-object="false"
            label="* "
            v-model="filters.driving__car"
            :items="$store.state.dashtbashiCars"
            item-text="car_number_str"
            item-value="id"

          />
        </v-col>
        <v-col cols="12" class="text-center">
          <m-datatable
            :headers="headers"
            api-url="dashtbashi/report/ladings/"
            :filters.sync="filters"
            @detail="(item) => newTab({name: 'Lading', params: {id: item.id}})"
          ></m-datatable>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import GetApi from "../GetApi";
export default {
  name: "LadingsList",
  mixins: [GetApi],
  data() {
    return {
      filters: {
        title: "     ",
      },
      headers: [
        {
          text: "",
          value: "local_id",
        },
        {
          text: " ",
          value: "remittance.code",
        },
        {
          text: " ",
          value: "lading_number",
        },
        {
          text: " ",
          value: "lading_date",
        },
        {
          text: "",
          value: "ware.name",
        },
        {
          text: "  ",
          value: "contractor_price",
          type: "numeric",
        },
        {
          text: "",
          value: "contractor.name",
        },
        {
          text: " ",
          value: "fare_price",
          type: "numeric",
        },
        {
          text: "",
          value: "origin.name",
        },
        {
          text: "",
          value: "destination.name",
        },
        {
          text: "  ",
          value: "destination_amount",
          type: "numeric",
        },
        {
          text: " ",
          value: "driving.driver.name",
        },
        {
          text: "",
          value: "driving.car.car_number_str",
          filterable: false,
          sortable: false,
        },
        {
          text: " ",
          value: "billNumber.number",
        },
        {
          text: " ",
          value: "bill_price",
          type: "numeric",
        },
        {
          text: "  ",
          value: "lading_total_value",
          type: "numeric",
        },
        {
          text: "   ",
          value: "lading_bill_total_value",
          type: "numeric",
        },
      ],
    };
  },
  mounted() {
    this.getCars();
  },
};
</script>

<style scoped lang="scss"></style>

